import React, { createRef, useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
    Card,
    CardHeader,
    CardContent,
    CardActions,
    Divider,
    Grid,
    Button,
    TextField,

} from '@material-ui/core';
import L from 'leaflet';
import axios from 'axios';
import { urlAddKec, urlEditKec, urlKab, urlProv, urlShowKab, urlShowKec, urlShowTargetKkPerProv } from '../../../../kumpulanUrl';
//import { Map, TileLayer, Marker, Popup, Tooltip } from 'components/LeafletComponent'
import validate from 'validate.js';
import { isArrayLiteralExpression, createTypeAliasDeclaration } from 'typescript';
import { LapPeriode } from '../../../../components';
const schema = {
    id_kecamatan: {
        presence: { allowEmpty: false, message: 'harus diisi' },
      },
};

const useStyles = makeStyles(theme => ({
    root: {},
    buttonSuccess: {
        color: theme.palette.white,
        backgroundColor: theme.palette.green,
        '&:hover': {
            backgroundColor: '#4caf50',
            borderColor: '#66bb6a',
            boxShadow: 'none',
        },
    },
    buttonCancel: {
        color: theme.palette.white,
        backgroundColor: theme.palette.red,
        '&:hover': {
            backgroundColor: '#f44336',
            borderColor: '#ef5350',
            boxShadow: 'none',
        },
    },
}));

const SensusPerKecamatanSearchModi = props => {
    const { className, setData, getDataBackend, setRowSelect, rowSelect, title, setKec, ...rest } = props;

    const classes = useStyles();

    const [values, setValues] = useState({});
    const [getStatus, setStatus] = useState([]);
    const [getKeyId, setKeyId] = useState([]);
    const [perKabupaten, setPerKabupaten] = useState([]);
    const [kabupaten, setKabupaten] = useState([]);
    const [perKecamatan, setPerKecamatan] = useState([])
    const [kecamatan, setKecamatan] = useState([]);
    const [prov, setProv] = useState([]);

    const status = [
        {
            value: '1',
            label: 'Active'
        },
        {
            value: '0',
            label: 'Inactive'
        }


    ];
    const [formState, setFormState] = useState({
        isValid: false,
        values: {},
        touched: {},
        errors: {}
    });

    async function showKec(id_kabupaten) {
        /* */
        const requestOptions = {
            method: 'POST',
            //mode: "cors",
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                "id_kabupaten": id_kabupaten,
            })
        };

        let urlShow = urlShowKec
        // eslint-disable-next-line no-useless-concat
        const response = await fetch(urlShow, requestOptions)
            .then(res => {
                return res.json();
            })

            .then(resJson => {
                const data = resJson;
                // console.log('kecamatan =', data.data)
                setKec(data.data);
                setKecamatan(data.data);
                //return false;
            })
            .catch(e => {
                //console.log(e);
                // alert("err");
                setKec([]);
                setKecamatan([]);
                //this.setState({ ...this.state, isFetching: false });
            });
    }

    async function showKab(id_provinsi) {
        /* */
        const requestOptions = {
            method: 'POST',
            //mode: "cors",
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                "id_provinsi": id_provinsi,
            })
        };

        let urlShow = urlShowKab
        // eslint-disable-next-line no-useless-concat
        const response = await fetch(urlShow, requestOptions)
            .then(res => {
                return res.json();
            })

            .then(resJson => {
                const data = resJson;
                // console.log('kabupaten =', data.data)
                setKec(data.data);
                setKabupaten(data.data);
                //return false;
            })
            .catch(e => {
                //console.log(e);
                // alert("err");
                setKec([]);
                setKabupaten([]);
                //this.setState({ ...this.state, isFetching: false });
            });
    }

    async function showTargetKkPerProv(Periode_Sensus) {
        /* */
        const requestOptions = {
          method: 'POST',
          mode: "cors",
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            "Periode_Sensus": Periode_Sensus,
          })
        };
    
        let urlShow = urlShowTargetKkPerProv
        // eslint-disable-next-line no-useless-concat
        const response = await fetch(urlShow, requestOptions)
          .then(res => {
            return res.json();
          })
    
          .then(resJson => {
            const data = resJson;
            setProv(data.data);
            //return false;
          })
          .catch(e => {
            //console.log(e);
    
            setProv([]);
            //this.setState({ ...this.state, isFetching: false });
          });
      }

    // async function getKab() {
    //   /* */
    //   const requestOptions={
    //     method: 'get',
    //     //mode: "cors",
    //     headers: { 'Content-Type': 'application/json' },
    //   };

    //   let urlGetKabAll=urlKab
    //   // eslint-disable-next-line no-useless-concat
    //   const response=await fetch(urlGetKabAll, requestOptions)
    //     .then(res => {
    //       return res.json();
    //     })

    //     .then(resJson => {
    //       const data=resJson;
    //       setPerKabupaten(data.data);
    //       //return false;
    //     })
    //     .catch(e => {
    //       //console.log(e);
    //       alert("Nextwork Error");
    //       setPerKabupaten([]);
    //       //this.setState({ ...this.state, isFetching: false });
    //     });
    // }


    ///  const mapRef=createRef();

    useEffect(() => {
        const errors = validate(rowSelect, schema);

        setFormState(formState => ({
            ...rowSelect,
            isValid: errors ? false : true,
            errors: errors || {}
        }));
        // console.log("formState", formState)


        //   alert(setOpen)
    }, [rowSelect]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour

    const handleChangePeriode = event => {
        handleChange(event)
        showTargetKkPerProv(event.target.value)
    }
    const handleChangeProvinsi = event => {
        handleChange(event)
        showKab(event.target.value)
    }
    const handleChangeKabupaten = event => {
        handleChange(event)
        showKec(event.target.value)
    }

    const handleChange = event => {

        //    event.persist();

        // const errors=validate(rowSelect, schema);

        setFormState(formState => ({
            ...rowSelect,
            // isValid: errors? false:true,
            // errors: errors||{}
        }));
        setRowSelect({
            ...rowSelect,
            [event.target.name]: event.target.value
        });
        let nama = event.target.name.replace("id", "nama")
        if (event.target.name == "id_kecamatan") {
            setRowSelect({
                ...rowSelect,
                [nama]: pencarian(kecamatan, event.target.value),
                [event.target.name]: event.target.value,
            });
            // console.log("Ket kecamatan =", kecamatan)
        }
    }


    const handleClose = () => {
        getDataBackend();
    }

    const handleSave = (event) => {
        getDataBackend(rowSelect)
    }

    const pencarian = (paramKec, id_kec) => {
        let value = id_kec
        let result = [];
        // alert(value)
        result = paramKec.filter((entry) => {
            return entry && entry.id_kecamatan && (entry.id_kecamatan === value)
        });
        // console.log("result =", result[0].nama_kecamatan)
        // alert("result = " + result[0].nama_kecamatan)
        return result[0].nama_kecamatan
    }


    //  const position=[currentLocation.lat, currentLocation.lng]
    const hasError = field => {
        return formState && formState.errors && formState.errors[field] ? true : false;
    }

    return (
        <Card
            {...rest}
            className={clsx(classes.root, className)}
        >

            <form
                autoComplete="off"
                noValidate
            >
                <CardHeader

                    subheader=""
                    title="Search Laporan Sensus"
                />
                <Divider />
                <CardContent>
                    <Grid
                        container
                        spacing={3}
                    >

                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            <LapPeriode
                                onChange={handleChangePeriode}
                                rowSelect={rowSelect}
                            />
                        </Grid>

                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            <TextField
                                fullWidth
                                label="Pilih Provinsi"
                                margin="dense"
                                name="id_provinsi"
                                onChange={handleChangeProvinsi}
                                select
                                value={rowSelect.id_provinsi}
                                variant="outlined"
                            >
                                {prov.map(option => (
                                    <option
                                        key={option.id_provinsi}
                                        value={option.id_provinsi}
                                    >
                                        {option.nama_provinsi}
                                    </option>
                                ))}

                            </TextField>

                        </Grid>

                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            <TextField
                                fullWidth
                                label="Pilih Kabupaten"
                                margin="dense"
                                name="id_kabupaten"
                                onChange={handleChangeKabupaten}
                                select
                                value={rowSelect.id_kabupaten}
                                variant="outlined"
                            >
                                {kabupaten.map(option => (
                                    <option
                                        key={option.id_kabupaten}
                                        value={option.id_kabupaten}
                                    >
                                        {option.nama_kabupaten}
                                    </option>
                                ))}

                            </TextField>

                        </Grid>
                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            <TextField
                                fullWidth
                                label="Pilih Kecamatan"
                                margin="dense"
                                name="id_kecamatan"
                                onChange={handleChange}
                                select
                                value={rowSelect.id_kecamatan}
                                variant="outlined"
                            >
                                {kecamatan.map(option => (
                                    <option
                                        key={option.id_kecamatan}
                                        value={option.id_kecamatan}
                                    >
                                        {option.nama_kecamatan}
                                    </option>
                                ))}

                            </TextField>

                        </Grid>
                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                        </Grid>
                    </Grid>
                </CardContent>
                <Divider />
                <CardActions>
                    {!formState.isValid}
                    <Button
                        color="primary"
                        className={classes.buttonSuccess}
                        variant="contained"
                        onClick={handleSave}
                        disabled={!formState.isValid}
                    >
                        Search
                    </Button>

                </CardActions>
            </form>
        </Card>
    );
};

SensusPerKecamatanSearchModi.propTypes = {
    className: PropTypes.string
};

export default SensusPerKecamatanSearchModi;
